*, :before, :after {
  box-sizing: border-box;
}

:root {
  --primary-color: #9bb537;
  --bg-color: #f8f8f8;
  --card-bg: #fff;
  --text-main: #050505;
  --text-secondary: #05050580;
  --border-color: #0505051a;
  --font-main: "Inter", sans-serif;
}

body.dark-theme {
  --bg-color: #161616;
  --card-bg: #202025;
  --text-main: #f8f8f8;
  --text-secondary: #f8f8f880;
  --border-color: #f8f8f833;
}

body {
  font-family: var(--font-main);
  background-color: var(--bg-color);
  color: var(--text-main);
  margin: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 1200px) {
  .container {
    max-width: 1240px;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.header {
  z-index: 10;
  background-color: #0000;
  width: 100%;
  height: 80px;
  position: absolute;
  top: 0;
  left: 0;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  display: flex;
}

.logo {
  color: var(--primary-color);
  text-transform: uppercase;
  letter-spacing: -.5px;
  font-size: 1.2rem;
  font-weight: 800;
}

@media (width <= 768px) {
  .nav.desktop-nav {
    display: none;
  }
}

.nav-list {
  gap: 40px;
  display: flex;
}

.nav-list .nav-link {
  color: var(--text-main);
  font-size: .95rem;
  font-weight: 500;
  transition: color .3s;
}

.nav-list .nav-link.active, .nav-list .nav-link:hover {
  color: var(--primary-color);
}

.header-controls {
  align-items: center;
  gap: 20px;
  display: flex;
}

.header-controls button {
  color: var(--text-main);
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  transition: color .3s, transform .2s;
  display: flex;
}

.header-controls button:hover {
  color: var(--primary-color);
  transform: scale(1.1);
}

.mobile-menu-btn {
  display: none;
}

@media (width <= 768px) {
  .mobile-menu-btn {
    display: block;
  }
}

.mobile-menu {
  background-color: var(--primary-color);
  z-index: 1000;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  padding: 20px;
  transition: transform .4s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu.is-hidden {
  transform: translateX(100%);
}

.mobile-menu .mobile-close-btn {
  color: #fff;
  align-self: flex-end;
  margin-bottom: 40px;
  font-size: 2.5rem;
}

.mobile-menu .mobile-nav-list {
  text-align: center;
  list-style: none;
}

.mobile-menu .mobile-nav-list li {
  margin-bottom: 25px;
}

.mobile-menu .mobile-nav-list a {
  color: #fff;
  text-transform: uppercase;
  font-size: 1.8rem;
  font-weight: 700;
}

.mobile-menu .mobile-nav-list a:hover {
  color: #f1f1f1;
}

.hero-section {
  margin-bottom: 40px;
  padding-top: 120px;
}

.hero-section .swiper {
  border-radius: 20px;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.hero-section .swiper-slide {
  background-color: var(--bg-color);
  flex-direction: column-reverse;
  padding: 20px;
  display: flex;
}

@media (width >= 768px) {
  .hero-section .swiper-slide {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 400px;
    padding: 0 50px;
  }
}

.hero-section .swiper-slide .hero-content {
  width: 100%;
}

@media (width >= 768px) {
  .hero-section .swiper-slide .hero-content {
    width: 50%;
  }
}

.hero-section .swiper-slide .hero-content h1 {
  margin-bottom: 20px;
  font-size: 2rem;
  line-height: 1.1;
}

.hero-section .swiper-slide .hero-content h1 span {
  color: var(--primary-color);
  font-style: italic;
}

@media (width >= 768px) {
  .hero-section .swiper-slide .hero-content h1 {
    font-size: 3rem;
  }
}

.hero-section .swiper-slide .hero-content .hero-btn {
  border: 2px solid var(--primary-color);
  color: var(--text-main);
  border-radius: 8px;
  padding: 12px 24px;
  font-weight: 500;
}

.hero-section .swiper-slide .hero-content .hero-btn:hover {
  background: var(--primary-color);
  color: #fff;
}

.hero-section .swiper-slide .hero-image {
  justify-content: center;
  width: 100%;
  display: flex;
}

@media (width >= 768px) {
  .hero-section .swiper-slide .hero-image {
    width: 40%;
  }
}

.hero-section .swiper-slide .hero-image img {
  max-height: 250px;
}

@media (width >= 768px) {
  .hero-section .swiper-slide .hero-image img {
    max-height: 350px;
  }
}

.hero-section .swiper-pagination-bullet-active {
  background: var(--primary-color) !important;
}

.search-recipes {
  padding-bottom: 60px;
}

.search-recipes__wrapp {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

@media (width >= 768px) {
  .search-recipes__wrapp {
    flex-direction: row;
    align-items: flex-start;
  }
}

.sidebar {
  flex-shrink: 0;
  width: 100%;
}

@media (width >= 768px) {
  .sidebar {
    width: 235px;
  }
}

.categories-box {
  margin-bottom: 30px;
}

.categories-box .all-categories-btn {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  width: 100%;
  color: var(--text-secondary);
  text-align: left;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 12px;
}

.categories-box .all-categories-btn.active {
  background: var(--primary-color);
  color: #fff;
  border-color: var(--primary-color);
}

.categories-box .categories-list {
  height: 300px;
  overflow-y: auto;
}

.categories-box .categories-list li button {
  text-align: left;
  width: 100%;
  color: var(--text-secondary);
  padding: 6px 0;
  font-size: 14px;
}

.categories-box .categories-list li button:hover, .categories-box .categories-list li button.active {
  color: var(--primary-color);
}

.categories-box .categories-list::-webkit-scrollbar {
  width: 4px;
}

.categories-box .categories-list::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}

.popular-recipes-box {
  display: none;
}

@media (width >= 1024px) {
  .popular-recipes-box {
    display: block;
  }
}

.popular-recipes-box .sidebar-title {
  margin-bottom: 15px;
  font-size: 18px;
}

.popular-recipes-box .popular-list li {
  cursor: pointer;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.popular-recipes-box .popular-list li img {
  object-fit: cover;
  border-radius: 6px;
  width: 50px;
  height: 50px;
}

.popular-recipes-box .popular-list li h4 {
  color: var(--text-main);
  margin: 0;
  font-size: 13px;
}

.popular-recipes-box .popular-list li p {
  color: var(--text-secondary);
  margin: 0;
  font-size: 11px;
}

.gallery-wrapper {
  flex-grow: 1;
  width: 100%;
}

.filters-wrap {
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 25px;
  display: flex;
}

.filters-wrap .filter-group {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.filters-wrap .filter-group label {
  color: var(--text-secondary);
  font-size: 12px;
}

.filters-wrap .filter-group input, .filters-wrap .filter-group select {
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  color: var(--text-main);
  border-radius: 8px;
  outline: none;
  padding: 10px;
}

.filters-wrap .filter-group input:focus, .filters-wrap .filter-group select:focus {
  border-color: var(--primary-color);
}

.filters-wrap .reset-btn {
  color: var(--text-secondary);
  align-self: flex-end;
  margin-bottom: 5px;
  font-size: 13px;
}

.recipes-grid {
  grid-template-columns: 1fr;
  gap: 20px;
  display: grid;
}

@media (width >= 768px) {
  .recipes-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 1200px) {
  .recipes-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.recipe-card {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  background-position: center;
  background-size: cover;
  border-radius: 15px;
  flex-direction: column;
  justify-content: flex-end;
  height: 300px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.recipe-card:before {
  content: "";
  background: linear-gradient(to top, #000c, #0000);
  position: absolute;
  inset: 0;
}

.recipe-card .card-icons {
  z-index: 2;
  color: #fff;
  font-size: 1.2rem;
  position: absolute;
  top: 15px;
  right: 15px;
}

.recipe-card .card-info {
  z-index: 2;
  color: #fff;
  padding: 15px;
  position: relative;
}

.recipe-card .card-title {
  text-transform: uppercase;
  margin: 0 0 5px;
  font-size: 1rem;
}

.recipe-card .card-desc {
  opacity: .8;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  margin-bottom: 10px;
  font-size: .8rem;
  display: -webkit-box;
  overflow: hidden;
}

.recipe-card .card-bottom {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.recipe-card .see-recipe-btn {
  background: var(--primary-color);
  color: #fff;
  border-radius: 6px;
  padding: 6px 12px;
  font-size: .8rem;
}

.backdrop {
  z-index: 999;
  background: #00000080;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.backdrop.is-hidden {
  display: none;
}

.modal {
  background: var(--card-bg);
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  color: var(--text-main);
  border-radius: 15px;
  padding: 30px;
  position: relative;
  overflow-y: auto;
}

.modal-close-btn {
  color: var(--text-main);
  font-size: 1.5rem;
  position: absolute;
  top: 10px;
  right: 10px;
}

.order-form, .rating-form {
  flex-direction: column;
  gap: 10px;
  margin-top: 15px;
  display: flex;
}

.order-form input, .order-form textarea, .rating-form input, .rating-form textarea {
  border: 1px solid var(--border-color);
  color: var(--text-main);
  background: none;
  border-radius: 5px;
  padding: 10px;
}

.submit-btn {
  background: var(--primary-color);
  color: #fff;
  border-radius: 5px;
  margin-top: 10px;
  padding: 12px;
}

.pagination-container {
  justify-content: center;
  gap: 5px;
  margin-top: 30px;
  display: flex;
}

.pagination-container .pagination-btn {
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  width: 35px;
  height: 35px;
  color: var(--text-main);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination-container .pagination-btn.active {
  background: var(--primary-color);
  color: #fff;
}
